 }
 .typeWriterDiv{
    margin-top: 3%;
    margin-left: 6%;
    align-items: center;
    display: inline-flex;
    background-color: #ffffff;
    
 }
 .typeWriterDiv p{
   font-size: 1.4em;
   font-weight: 500;
 }

 .typeWriterText{
      font-size: 1.5em;
      font-style: oblique;
      display: inline;
      font-weight: 700;
      font-size: 1.8em;
      -webkit-animation: changeTypeWriterTextColor 10s step-end infinite;
              animation: changeTypeWriterTextColor 10s step-end infinite;
 }

 @-webkit-keyframes changeTypeWriterTextColor {
   0% {
     color: red
   }
   25% {
     color: green
   }
   50% {
     color: orange
   }
   75% {
     color: blue
   }
 }

 @keyframes changeTypeWriterTextColor {
   0% {
     color: red
   }
   25% {
     color: green
   }
   50% {
     color: orange
   }
   75% {
     color: blue
   }
 }

.login-contents {
   display: flex;
   width: 22rem;
   flex-direction: column;
   justify-content: center;
   background-color: #ffffff;
   border-radius: 12px;
   margin-top: 3%;
}
.login-btn {
   width: 18rem;
   padding: 0.7rem 1rem;
   margin: 2rem;
   /* margin-top: 1rem; */
   font-size: 1.3rem;
   font-weight: 500;
   cursor: pointer;
   background-color: #1e88e5;
   color: #ffffff;
   border-style: none;
   border-radius: 10px;
   border-color: black;
   transition: -webkit-transform 300ms ease;
   transition: transform 300ms ease;
   transition: transform 300ms ease, -webkit-transform 300ms ease;
}
.login-btn:hover {
   -webkit-transform: scale(1.1);
           transform: scale(1.1);
}
.img-slider {
