}


.about-navbar a {
   background-color: #3AA3FB;
   color: whitesmoke;
   font-size: 1.5rem;
   text-decoration: none;
   margin-left: 1rem;
   position: fixed;
   top: 0.8rem;
   
}
.about-navbar a:hover {
   -webkit-transform: scale(1.05);
