}
.user-email {
   color: white;
   margin-bottom: 0.5rem;
   font-size: 1.1rem;
}
.social-tab a {
   opacity: 0.8;
   color: #3AA3FB;
   margin-top: 01rem;
   padding: 0.5rem;
